import { Accordion, AccordionDetails, AccordionSummary, Button, Stack, Tooltip } from '@mui/material';
import { bidvLogin, bidvScanQR, bidvConfirm, bidvConfirmBeforeFace, bidvConfirmAfterFace } from '../../services/handle.service';
import ExpandMore from '@mui/icons-material/ExpandMore';
import { grey } from '@mui/material/colors';

const HandleBIDV = ({ item, X, Y, setLoading }) => {
  return (
    <Accordion
      disableGutters
      square
      sx={{
        boxShadow: 0,
        border: `1px solid ${grey[400]}`,
        '&:not(:last-child)': {
          borderBottom: 0
        },
        '&::before': {
          display: 'none'
        }
      }}
    >
      <AccordionSummary expandIcon={<ExpandMore />}>BIDV</AccordionSummary>
      <AccordionDetails>
        <Stack spacing={1}>          
          <Tooltip title="Nhp mt khu v click ng nhp" arrow>
            <Button
              size="small"
              variant="contained"
              color="inherit"
              fullWidth
              onClick={() => bidvLogin({ device_id: item.id, X, Y }, setLoading)}
            >
              ng nhp
            </Button>
          </Tooltip>
          <Tooltip title="Thao tc qut QR" arrow>
            <Button
              size="small"
              variant="contained"
              color="inherit"
              fullWidth
              onClick={() => bidvScanQR({ device_id: item.id, X, Y }, setLoading)}
            >
              Qut QR
            </Button>
          </Tooltip>
          {/* <Tooltip title="Chn nh QR trong th vin" arrow>
            <Button
              size="small"
              variant="contained"
              color="inherit"
              fullWidth
              onClick={() => clickScanQRBIDV({ device_id: item.id, X, Y }, setLoading)}
            >
              Qut QR
            </Button>
          </Tooltip> */}
          {/* <Tooltip title="Click Xc nhn)" arrow>
            <Button
              size="small"
              variant="contained"
              color="inherit"
              fullWidth
              onClick={() => bidvTransferAndConfirm({ device_id: item.id, X, Y }, setLoading)}
            >
              Xc nhn
            </Button>
          </Tooltip> */}
          <Tooltip title="Xc nhn (sau qut QR))" arrow>
            <Button
              size="small"
              variant="contained"
              color="inherit"
              fullWidth
              onClick={() => bidvConfirm({ device_id: item.id, X, Y }, setLoading)}
            >
              Xc nhn (sau qut QR)
            </Button>
          </Tooltip>
          <Tooltip title="M PIN & Xc nhn)" arrow>
            <Button
              size="small"
              variant="contained"
              color="inherit"
              fullWidth
              onClick={() => bidvConfirmBeforeFace({ device_id: item.id, X, Y }, setLoading)}
            >
              Xc nhn (trc qut mt)
            </Button>
          </Tooltip>
          <Tooltip title="M PIN & Xc nhn)" arrow>
            <Button
              size="small"
              variant="contained"
              color="inherit"
              fullWidth
              onClick={() => bidvConfirmAfterFace({ device_id: item.id, X, Y }, setLoading)}
            >
              Xc nhn (sau qut mt)
            </Button>
          </Tooltip>
        </Stack>
      </AccordionDetails>
    </Accordion>
  );
};

export default HandleBIDV;