import axios from '../utils/axios';
import { swalNotification } from '../utils/swal';

export const endpoints = {
  key: 'hide-usb',
  actionHideUSB: '/action-hide-usb'
};

export async function postActionHideUSB(data) {
  try {
    const response = await axios.post('/hideUSBDebugging/action-hide-usb', data);

    if (!response.data.valid) {
      swalNotification('warning', 'Thng bo', response.data.message);
    }

    return response.data;
  } catch (error) {
    swalNotification('error', 'Li', error.message);
    return {
      status: false,
      msg: error.message
    };
  }
}
