import {
  Accordion, AccordionDetails, AccordionSummary, Stack, Tooltip,
  Button, Dialog, DialogTitle, DialogContent, DialogActions,
  TextField, Select, MenuItem, FormControl, InputLabel, Box, Autocomplete
} from '@mui/material';
import ExpandMore from '@mui/icons-material/ExpandMore';
import { grey } from '@mui/material/colors';
import { useEffect, useState } from 'react';
import { downloadQrForAccount } from '../api/device';

const HandleTestQR = ({ item }) => {
  const [expand, setExpand] = useState(false);
  const [open, setOpen] = useState(false);
  const [bankData, setBankData] = useState([]);
  const [selectedEntry, setSelectedEntry] = useState('');
  const [amount, setAmount] = useState('');

  useEffect(() => {
    const loadBanks = () => {
      const stored = localStorage.getItem('local-banks');
      if (stored) {
        try {
          const parsed = JSON.parse(stored);
          setBankData(parsed);
        } catch (e) {
          console.error('Li c local-banks t localStorage:', e);
        }
      }
    };
  
    loadBanks();
    window.addEventListener('banks-updated', loadBanks);
  
    return () => window.removeEventListener('banks-updated', loadBanks);
  }, []);

  const handleGenerateQR = async () => {
    if (!selectedEntry || !amount) {
      alert('Vui lng nhp  thng tin.');
      return;
    }
    const [bank_code, bank_account] = selectedEntry.split('|');

    const result = await downloadQrForAccount({
      bank_code,
      bank_account,
      device_id: item.id,
      amount
    });

    if (!result.status) {
      alert(result.message || 'Khng th to QR');
    } else {
      alert(`QR  c to v lu vo /sdcard/DCIM/Camera/${item.id}_vietqr.png`);
      console.log('QR created successfully:', result.vietqr_url);
      setOpen(false);
      setSelectedEntry('');
      setAmount('');
    }
  };

  return (
    <>
      <Accordion
        disableGutters
        square
        sx={{ boxShadow: 0, border: `1px solid ${grey[400]}`, '&::before': { display: 'none' } }}
        onChange={(event, expanded) => { setExpand(expanded); }}
      >
        <AccordionSummary expandIcon={<ExpandMore />}>TEST TH</AccordionSummary>
        <AccordionDetails>
          <Stack spacing={1} alignItems="center" justifyContent="space-between">
            <Tooltip title="Test th" arrow>
              <Button
                size="small"
                variant="contained"
                color="inherit"
                fullWidth
                onClick={() => setOpen(true)}
              >
                Test th
              </Button>
            </Tooltip>
          </Stack>
        </AccordionDetails>
      </Accordion>

      <Dialog
        open={open}
        onClose={() => setOpen(false)}
        maxWidth="xs"
        fullWidth
        PaperProps={{
          sx: {
            mx: 'auto',
            width: '100%',
            maxWidth: 400,
          }
        }}
      >
        <DialogTitle textAlign="center">To QR chuyn tin</DialogTitle>
        <DialogContent>
          <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2, pt: 1 }}>
            {/* <FormControl fullWidth variant="outlined" size="small">
              <InputLabel id="bank-entry-label">NGN HNG - S TI KHON</InputLabel>
              <Select
                labelId="bank-entry-label"
                value={selectedEntry}
                label="NGN HNG - S TI KHON"
                onChange={(e) => setSelectedEntry(e.target.value)}
                MenuProps={{ PaperProps: { sx: { maxHeight: 300 } } }}
              >
                {Array.isArray(bankData) && bankData.map((entry, idx) => (
                  <MenuItem
                    key={idx}
                    value={`${entry['NGN HNG']}|${entry['S TI KHON']}`}
                  >
                    {`${entry['NGN HNG']} - ${entry['S TI KHON']}${entry['TN CH TH'] ? ' - ' + entry['TN CH TH'] : ''}${entry['TN'] ? ' - ' + entry['TN'] : ''}`}
                  </MenuItem>
                ))}
              </Select>
            </FormControl> */}
            <Autocomplete
              fullWidth
              size="small"
              options={bankData}
              getOptionLabel={(option) =>
                `${option["NGN HNG"]} - ${option["S TI KHON"]} - ${option["TN CH TH"] || ''}`.trim()
              }
              renderInput={(params) => (
                <TextField {...params} label="Tm kim ngn hng / s TK / tn ch th" variant="outlined" />
              )}
              filterOptions={(options, { inputValue }) => {
                const keyword = inputValue.toLowerCase().trim();
                return options.filter(opt =>
                  opt["NGN HNG"]?.toLowerCase().includes(keyword) ||
                  opt["S TI KHON"]?.toLowerCase().includes(keyword) ||
                  opt["TN CH TH"]?.toLowerCase().includes(keyword)
                );
              }}
              onChange={(event, value) => {
                if (value) {
                  setSelectedEntry(`${value["NGN HNG"]}|${value["S TI KHON"]}`);
                }
              }}
            />
            <TextField
              fullWidth
              label="S tin"
              value={amount}
              onChange={(e) => setAmount(e.target.value)}
              size="small"
              type="number"
            />
          </Box>
        </DialogContent>
        <DialogActions sx={{ px: 3, pb: 2 }}>
          <Button onClick={() => setOpen(false)} color="inherit">Hy</Button>
          <Button onClick={handleGenerateQR} variant="contained">To QR</Button>
        </DialogActions>
      </Dialog>
    </>
  );
};

export default HandleTestQR;